* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid;
}

.card {
  align-content: center;
  border: 1px solid #d5d5d5;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  border-radius: 10px;

}

.card:hover {
  -webkit-box-shadow: -4px -7px 60px -18px rgba(94,94,94,1);
  -moz-box-shadow: -4px -7px 60px -18px rgba(94,94,94,1);
  box-shadow: -4px -7px 60px -18px rgba(94,94,94,1);
}


.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

.cover{
  object-fit: cover;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}